import { useState } from "react";
import { useToast } from "@/hooks/use-toast";

// استخدم متغير بيئي لرابط API
const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:3001";

const useAuth = () => {
    const [emailCheckLoading, setEmailCheckLoading] = useState(false);
    const { toast } = useToast();

    const checkEmailExists = async (email: string, role: "host" | "student") => {
        try {
            setEmailCheckLoading(true);

            const response = await fetch(`${API_URL}/api/check-email`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                credentials: "include",
                body: JSON.stringify({ email, role }),
            });

            if (!response.ok) {
                throw new Error("Failed to verify email. Please try again.");
            }

            const data = await response.json();
            return data.exists;
        } catch (err: any) {
            toast({
                title: "Error",
                description: err.message || "Failed to verify email. Please try again.",
                variant: "destructive"
            });
            return false;
        } finally {
            setEmailCheckLoading(false);
        }
    }

    return { checkEmailExists, emailCheckLoading };
}

export default useAuth;